<template>
  <el-container style="height: 100%; border: 0px solid #eee">
    <el-main>
      <br/>
      <div class="md" style="text-align: left; overflow-y:auto;">
        <div class="content" v-html="htmlContent">
        </div>
      </div>

      <br />
      <el-row>
        <el-button @click="handle(1)" circle>1</el-button>
        <el-button @click="handle(2)" type="primary" circle>2</el-button>
        <el-button @click="handle(3)" type="success" circle>3</el-button>
        <el-button @click="handle(4)" type="info" circle>4</el-button>
        <el-button @click="handle(5)" type="warning" circle>5</el-button>
        <el-button @click="handle(6)" type="danger" circle>6</el-button>
      </el-row>
    </el-main>

  </el-container>
</template>

<script>
  import axios from 'axios';
  export default {

    data () {
      return {
        htmlContent: "",
      }
    },
    mounted() {
      this.handle(1)
    },
    methods: {
      async handle(i) {
          this.htmlContent = ""
          await axios.get("/static/home/" +  i + ".md").then(res =>{
              this.htmlContent = this.converter.makeHtml(res.data);
          })
      },
    }
  }
</script>
<style scoped>
  .el-container {
    height: 100%;
    width: 100%;
    position: fixed;
    overflow: auto;
    background-image: linear-gradient(#c7edcc, #54FF9F);
  }

  .el-main {
    overflow-y:hidden;
    margin-top: 0px;
    margin-left: 0px;
    height: 100%;
    border: 1px;
    padding: 30px;
  }

  .md {
    border: 3px solid white;
    border-radius: 25px;
    width: 900px;
    height: 75%;
    margin: 0 auto;
    font-size: 20px;
    padding: 10px;
    display: block;

    text-overflow: ellipsis;
    overflow-y:auto;
  }

  /* 自定义滚动条样式 */
  .md::-webkit-scrollbar {
    width: 10px; /* 设置滚动条宽度 */
  }

  .content {
    margin-left: 20px;
    margin-right: 20px;
  }
  .el-button {
    min-height: 40px;
    min-width: 40px;
  }
</style>



